.container-card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'PT Serif', serif;
    margin: 4em;
    background-color: transparent;
    border: none;
    outline: none;
    width: 190px;
    height: 230px;
    border-radius: 9px;
    box-shadow: 0px 0px 38px -8px #52658899;
}

.card-icon {
    font-size: 7em;
    flex-grow: 2;
    pointer-events:none;
}

.card-text {
    width: 90px;
    font-size: 22px;
    pointer-events:none;
}