/* css from menu button and the drawer that open as the user click*/
.button-menu {
    position:absolute;
    z-index:12;
    font-size: 40px;
    left: 16px;
    top:12px;
    color: rgb(22, 21, 82);
}
/* end of the menu button and drawer css
========================================
 css from menu list inside drawer*/
.drawer-show{
    position: fixed;
    z-index:10;
    top:60px;
    height: 100vh;
    width: 240px;
    margin: 0 auto;
    padding-left: 0px;
    text-align: center;
    background-color: rgb(255, 255, 255);
    box-shadow:2px 4px 10px #5983d152;
    transition: ease-in 0.3s;   

}
.drawer-hide {  
    position: fixed;
    z-index:10;
    top:60px;
    height: 100vh;
    margin: 0 auto;
    padding-left: 0px;
    text-align: center;
    width: 0px;    
    transform: translateX(-1000px);
    transition: ease-out 1s;    

}
/* css from menu button and the button that opens the subnav inide drawer*/
.menu-list{
    position: sticky;
    height: fit-content;
    list-style-type: none;
    text-align: left;
    margin-left: 19px;
    padding-left: 0px;
    padding-top: 10px;
    margin-top: 10px;
    border-left: 2px solid #32c2db;
    background-color: rgba(255, 255, 255, 0.808);

}
.menu-button{
    width: 90%;
    height: 30px;
    padding: 0px;
    padding-left: 40px;
    margin-bottom: 18px;
    font-size: 22px;
    text-align:left;
    outline: none;
    border:transparent;
    background-color: transparent;
}
.menu-button:hover{
    color: rgb(3, 4, 22);
    font-weight: bolder;
}
.selected{
    padding:12px;
    padding-left: 0px;
    border-radius: 0 60px 60px 0;
    background: linear-gradient(to right, #1aead77d, #ffffff);
    box-shadow:2px 4px 10px #5983d152;
    opacity: 1;
    height: 100px;
}
.submenu-closed{
    position: sticky;
    list-style-type: none;
    height: 0px;
    opacity: 0;
    visibility: hidden;
    transition: height 2s,opacity 2s;
}
.submenu-open {   
    height: fit-content;
    background-color: #03083afd;
    transition: height 2s,opacity 2s;

}
.submenu-button{
    width: 85%;
    height: 30px;
    font-size:20px;
    padding-left: 40px;
    text-align:left;
    margin-top: 8px;
    margin-bottom: 8px;
    border: none;
    background-color: transparent;    
    color: white;

}
.submenu-button:hover{
    color:#F8F6F7;
    font-weight: bolder;
}







/* end of the menu button and drawer css
