.page-index{
    margin: 0 auto;
    width: 70%;
    height: 30%;
    box-shadow: 2px 1px 10px #5983d177;
    border-radius: 5px;
}
.index-btn-area{
    margin: 0 auto;
    text-align: center;
    margin-top: 18px;
    width: 80%;
    height: 4%;
    border-radius: 4px;

}
.index-btn{
    width: 40px;
    height: 100%;
    margin-left: 6px;
    color: #ffffff;
    font-weight: bold;
    border:none;
    background-color: #0059ff;
    box-shadow: 2px 1px 10px #5983d177;
    outline: none;
    transition: all 0.3s;

}
.index-btn:hover{
    color: rgb(0, 0, 0);
    background-color: #114cbb;

}
.active{
    color: rgb(0, 0, 0);
    background-color: #114cbb;

}