.Navbar {
    display: flex;
    height: 60px;
    width: 100%;
    background: #658BEB;
    justify-content: center;
    align-items: baseline;
}
.logo-container {
    position: absolute;
    left: 0px;
    width: 300px;
    align-self: left;
    text-align: center;
    font-weight: bold;
    color: #34529E;
    font-size: 26px;
    font-family: 'Cormorant Unicase', serif;
}
.FaTree {
    font-size: 50px;
    color: #34529E;
}
.header-main {
    text-align: center;
    height: 60px;
}
.SearchBar {
    margin: 0 auto;
    display: flex;
    width: fit-content;
    height: 100%;
    justify-content: center;
    padding-top: 13px;
}
.FaSearch {
    color: rgba(255, 255, 255, 0.979);
    position: sticky;
    justify-content: center;
    font-size: 20px;
    margin-top: 6px;
    margin-left: -33px;
}
.form-control, .SearchBar .form-control:focus {
    border: 1px solid #090c2fa2;
    font-size: 13px;
    font-weight: bold;
    height: 34px;
    padding-left: 20px;
    padding-right: 40px;
    background: #34529E;
    color: white;
    box-shadow: none;
    border-radius: 4px;
    outline: none;
    width: 300px;
}

.form-control::placeholder {
    color: #ffffff;
    opacity: 1;
}

.user-menu-button{
    position: absolute;
    right: 2em;
    top: 1.5em;
    outline: none;
    border: none;
    color: #fff;
    font-weight: bold;

    background-color: transparent;
}
