.first-form{
    width: 65%;
    text-align:center;
    display:list-item;
    height: fit-content;
    margin: 0 auto;
    padding-top: 3%;
    list-style-type: none;
}

.left-line-input{    
    outline: none;
    border: none;
    padding-top: 5px;
    height: 30px;
    width: 40%;
    margin-right: 6%;
    margin-bottom: 3%;
    font-size:17px;
    border-bottom:2px solid rgba(11, 37, 51, 0.192);
    transition: all 1s;

}
.left-line-input:hover{
    background-color:  rgba(11, 37, 51, 0.055);

    
}
.right-line-input{    
    outline: none;
    border: none;
    padding-top: 5px;
    height: 30px;
    width: 40%;
    margin-bottom: 3%;
    font-size:17px;
    border-bottom:2px solid rgba(11, 37, 51, 0.192);
    transition: all 1s;

}
.right-line-input:hover{
    background-color:  rgba(11, 37, 51, 0.055);

    
}
.second-line-input{    
    outline: none;
    border: none;
    padding-top: 5px;
    height: 30px;
    width: 86%;
    margin-bottom: 3%;
    font-size:17px;
    border-bottom:2px solid rgba(11, 37, 51, 0.192);
    transition: all 1s;

}
.second-line-input:hover{
    background-color:  rgba(11, 37, 51, 0.055);

}
.checkbox{
    outline: none;
    background-color:#0a3cc5cc;
    color: #ffffff;
    font-size: 15px;
    padding: 10px 20px;
    border: 1px solid transparent;  
    outline: none;
    border: none;
    padding-top: 5px;
    margin-bottom: 3%;
    margin-left: 2%;
    border-radius: 4px;
    border-bottom:2px solid rgba(11, 37, 51, 0.192);
    transition: all 1s;
}
.first-form  option {
    background-color:#042d9ecc;
    height: 10px;
}
.btn-area{
    margin: auto;
    display: flex;
    width: 90%;
    height: fit-content;
}
.send-form-area{
    text-align: right;
    margin-top: 4%;
}
.submit-form{
    width: 20%;
    margin-right: 1%;
    margin-bottom: 6px;
    color: #fff;
    background: #2caa0c;
    border: none;
    padding: 10px;
    border-radius: 4px;
    border-bottom: 4px solid #267c10;
    transition: all .2s ease;
    outline: none;
    text-transform: uppercase;
    font-weight: 700;
}
.submit-form:hover {
    background: #258d0a;
    color: #ffffff;
    transition: all .2s ease;
    cursor: pointer;
}
.submit-form:active {
    border: 0;
    transition: all .2s ease;
  }
