
.login-box {
  margin: auto;
  height: 515px;
  width: 460px;
  padding: 10px;
  margin-top: 3%;
  text-align: center;
  background: #090c2f87;
  border-radius: 28px;
  box-shadow: -22px 23px 40px 5px rgba(0, 0, 0, 0.47);
}

.icon {
  text-align: center;
  font-size: 70px;
  color: #fff;
  opacity: 0.5;
}

.form-signup {
  display: inline-flex;
  justify-content: space-around;
  flex-flow: column;
  align-items: center;
  height: 100%;
  width: 90%;
}

.form-login {
  display: inline-flex;
  justify-content: space-around;
  flex-flow: column;
  align-items: center;
  height: 80%;
  width: 70%;
}

.login-title {
  font-size: 24px;
  letter-spacing: 2px;
  font-weight: bold;
  color: #ecf0f5;
  margin-bottom: 20px;
}

#form::placeholder {
  color: #f8f8f8;
  opacity: 1;
}

.login {
  background: transparent;
  border: 0 solid;
  border-bottom: 1px solid rgb(255, 250, 255);
  color: white;
  height: 7%;
  transition: 150ms background ease-in;
  width: calc(100% - 3rem);
}

.signup {
  background: transparent;
  border: 0 solid;
  border-bottom: 1px solid rgb(255, 250, 255);
  color: white;
  height: 7%;
  transition: 150ms background ease-in;
  width: 70%;
}

.login:hover {
  color: #ffffff;
  border-bottom: 2px solid #7cb3d3;
  background-color: #3d93daf1;
  box-shadow: 0px 0px 4px 1px rgb(255, 255, 255);
  transition: 0.3s;
  background-color: #3d93daf1;
}

.form-submit-area {
  align-self: flex-end;
  width: 100%;
}

.login-Button {
  box-shadow: -5px 6px 24px 1px rgba(2, 0, 0, 0.33);
  color: #ffffff;
  border: 2px solid #7cb3d3;
  background: linear-gradient(202deg, #409ae4, #2978b9);
  text-decoration: none;
  border-radius: 28px;
  border: transparent;
  padding: 12px 23px;
  font-size: 12px;
  letter-spacing: 2px;
  font-weight: bold;
  transition: 0.6s;
}

.login-Button:hover {
  border-bottom: 1px solid rgb(255, 250, 255);
  background-color: transparent;
}

.login-Button:active {
  position: relative;
  top: 1px;
}